import React from 'react';
import { Popover as HeadlessPopover, Transition } from '@headlessui/react';
import classNames from 'classnames';

export const popoverPanelStyles = 'absolute z-10 right-0';

export const PopoverPanel: React.FC<React.HTMLAttributes<HTMLDivElement>> = ({
  className,
  children,
  ...props
}) => {
  return (
    <Transition
      enter="transition duration-100 ease-out"
      enterFrom="transform scale-95 opacity-0"
      enterTo="transform scale-100 opacity-100"
      leave="transition duration-75 ease-out"
      leaveFrom="transform scale-100 opacity-100"
      leaveTo="transform scale-95 opacity-0"
    >
      <HeadlessPopover.Panel className={classNames(popoverPanelStyles, className)} {...props}>
        {children}
      </HeadlessPopover.Panel>
    </Transition>
  );
};
